import {
  Card,
  Col,
  // Container,
  Row,
  CardBody,
  // CardImg,
  // CardHeader,
  // CardTitle,
  // FormGroup,
  // Label,
  // Button,
  // Form,
  // Input,
  // InputGroupAddon,
  // InputGroupText,
  // InputGroup,
  // Breadcrumb,
  // BreadcrumbItem,
  ListGroup,
  ListGroupItem,
} from "reactstrap"
// import CIcon from '@coreui/icons-react'

import logo from '../../../src/assets/ot.png'


import { CContainer, CFade } from '@coreui/react'

