import {
  Card,
  Col,
  // Container,
  Row,
  CardBody,
  CardImg,
  // CardHeader,
  // CardTitle,
  // FormGroup,
  // Label,
  // Button,
  // Form,
  // Input,
  // InputGroupAddon,
  // InputGroupText,
  // InputGroup,
  // Breadcrumb,
  // BreadcrumbItem,
} from "reactstrap"
// import CIcon from '@coreui/icons-react'

// import logo from '../../../src/assets/ot.png'


import { CContainer, CFade } from '@coreui/react'

// import ReactSelect from 'react-select'
// import makeAnimated from 'react-select/animated';

// import conf from '../../conf.js';
// import { storage } from "../../firebase/firebase";
import $ from 'jquery';
import { format } from 'date-fns';

// import ProgrammeTournoiEcole from './ProgrammeTournoiEcole'
import ProgrammeTournoiEntreprise from './ProgrammeTournoiEntreprise'
import InscriptionTournoiEcole from './InscriptionTournoiEcole'
// import ResultatTournoiEcole from './ResultatTournoiEcole'
import ResultatTournoiEntreprise from './ResultatTournoiEntreprise'
import ReactDOM from 'react-dom';
// import ville from '../dict/ville.js'
// import HeaderBa from '../../containers/TheHeaderBa'
// import  { Redirect } from 'react-router-dom'

// import TheHeaderVisiteur from '../../containers/TheHeaderVisiteur'
// import TheFooter from '../../containers/TheFooter'



const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

class DetailTournoiEcole extends Component {
  constructor(props) {
      super(props)
      this.state = {
        ok:"",
        ecole:{},
        logo:JSON.parse(localStorage.getItem('detail_tournoi_visiteur')).logo,
        matchs:JSON.parse(localStorage.getItem('detail_tournoi_visiteur')).matchs
      }
  }
  handleProgramme=()=>{
    ReactDOM.render(<div>{<ProgrammeTournoiEntreprise matchs={this.state.matchs}/>}</div>, document.getElementById('page'));
  }
  handleParticiper=()=>{
    //localStorage.setItem('idAcceuilTournoi',id);
    ReactDOM.render(<div>{<InscriptionTournoiEcole/>}</div>, document.getElementById('page'));
  }
  handleResultat=()=>{
    ReactDOM.render(<div>{<ResultatTournoiEntreprise matchs={this.state.matchs}/>}</div>, document.getElementById('page'));
    
  }
  
  componentDidMount() {
    const data=JSON.parse(localStorage.getItem('detail_tournoi_visiteur'))
    console.log("daata===",data)
    this.setState({ecole:JSON.parse(localStorage.getItem('detail_tournoi_visiteur'))})
    try{
      $("#debut").val(format(new Date(data.date_debut),'dd/MM/yyyy'))
    }catch(error){
      $("#debut").val("Non définie")
    }

    try{
      $("#fin").val(format(new Date(data.date_fin),'dd/MM/yyyy'))
    }catch(error){
      $("#fin").val("Non définie")
    }

    try{
      $("#inscription").val(format(new Date(data.date_fin_inscription),'dd/MM/yyyy'))
    }catch(error){
      $("#inscription").val("Non définie")
    }

    
    try{
      $("#inscription-debut").val(format(new Date(data.date_debut_inscription),'dd/MM/yyyy'))
    }catch(error){
      $("#inscription").val("Non définie")
    }
    
    
  }

  render() {
    // const animatedComponents = makeAnimated();
    
    var today = new Date();
    var Annee = today.getFullYear()

  return (
    <>
    
    <CContainer fluid>
    {this.state.ok}
    <Suspense fallback={loading}>
    <CFade>

      
    {/* <TheHeaderVisiteur/> */}

      <div className="mt-5"></div>

      <section id="contact" class="contact">
        <div class="container">

          <div className="mt-5">
            <div class="section-title">
              <h2>Tournoi inter-écoles des écoles supérieures - {Annee}</h2>
            </div>
          </div>

          <Row> 
            <Card>
             
              
              
              
              <CardBody>
                <CardImg style={{height:"200px"}} src={this.state.logo} alt="Image tournoi inter-ecole" />
                {/* <CardTitle tag="h5">{element.titre}</CardTitle>
                <CardSubtitle tag="h6" className="mb-2 text-muted">bouton: {element.bouton}</CardSubtitle>
                <CardText>{element.description}</CardText> */}

                <Row className="mt-4">
                  <Col> 

                    
                    <div  
                      
                      class="php-email-form"
                    >
                      <div class="form-row">

                        <div class="form-group col-md-6" >
                          <div className="mt-lg-5"> </div>
                          {/* <textarea type="text" class="form-control" name="nomequipe" id="nomequipe" readOnly/> */}
                          Participez à ce tournoi en vous inscrivant à seulement <strong> {this.state.ecole.prix} FCFA </strong>. <br/>
                          Venez défier d'autres équipes
                          <div class="validate"></div>
                        </div>

                        <div class="form-group col-md-6" >
                          <h4 className="text-center"> Informations </h4>

                          <div class="form-group">
                            <label for="name">Date de debut des inscriptions</label>
                            <input type="text" class="form-control" name="subject" id="inscription-debut" readOnly/>
                            <div class="validate"></div>
                          </div>

                          <div class="form-group">
                            <label for="name">Date de fin des inscriptions</label>
                            <input type="text" class="form-control" name="subject" id="inscription" readOnly/>
                            <div class="validate"></div>
                          </div>

                          <div class="form-group">
                            <label for="name">Début du tournoi</label>
                            <input type="text" class="form-control" name="subject" id="debut" readOnly/>
                            <div class="validate"></div>
                          </div>

                          <div class="form-group">
                            <label for="name">Fin du tournoi</label>
                            <input type="text" class="form-control" name="subject" id="fin" readOnly />
                            <div class="validate"></div>
                          </div>

                          <div class="mb-3 mt-5">
                            <div class="loading">Chargement</div>
                            <div class="error-message"></div>
                            <div class="sent-message">Votre message a été envoyé. Merci!</div>
                          </div>
                       {
                          (new Date(this.state.ecole.date_debut_inscription)>=new Date() && new Date(this.state.ecole.date_fin_inscription)<=new Date() )?
                        (<>
                         <div class="text-center">
                            <button id="submit" onClick={this.handleParticiper} >Participer</button>    <button id="submit" onClick={this.handleResultat}>Résultat</button>
                          </div>

                        
                          <div class="text-center" style={{display:"none"}} className="mt-3 text-center"> <a class="a" onClick={this.handleProgramme}><u>  Programme des matchs </u> </a> </div>
                        </>):null
                       }
                        </div>
                      </div>
                    </div>
                  
                  </Col>
                </Row>

              </CardBody>
            </Card>
          </Row> 


        </div>
      </section>

      {/* <TheFooter/> */}

    </CFade>
    </Suspense>
    </CContainer>

    </>
  )
}
}

export default DetailTournoiEcole
