import {
  // Card,
  // Col,
  // Container,
  // Row,
  // CardBody,
  // CardHeader,
  // CardTitle,
  // FormGroup,
  // Label,
  // Button,
  // Form,
  Input,
  // InputGroupAddon,
  // InputGroupText,
  // InputGroup,
  // Breadcrumb,
  // BreadcrumbItem,
} from "reactstrap"
// import CIcon from '@coreui/icons-react'

// import logo from '../../../src/assets/ot.png'

import  { Redirect } from 'react-router-dom'
import { CContainer, CFade } from '@coreui/react'

import ReactSelect from 'react-select'
import makeAnimated from 'react-select/animated';

import conf from '../../conf.js';
import { storage } from "../../firebase/firebase";
import $ from 'jquery'
// import Success from '../pages/messages/Success'
import ville from '../dict/ville.js'

// import TheHeaderVisiteur from '../../containers/TheHeaderVisiteur'
// import TheFooter from '../../containers/TheFooter'
import ModalMessage from '../pages/messages/ModalMessage'


const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

const optionTypeEntreprise = [
  {
    options: [
      { label: "SRL", value: "srl" },
      { label: "SA", value: "sa" },
      { label: "PME", value: "pme" }
    ]
  },
]

class InscriptionTournoiEntreprise extends Component {
  constructor(props) {
      super(props)
      this.state = {
        ville:"",
        type:"",
        ok:"",
        files_preview:"",
        files_name:{},
        multimedia:{file:""},
        show:false,
        success:true,
        boutton:"OK",
        message:"Enregistré avec succès !!",
        titre:"Message participation tournoi"
      }
      this.handleChangeVille.bind(this)
      this.handleChangeType.bind(this)
      this.handleAddTransaction.bind(this)
  }
  
  componentDidMount() {
    console.log("id",localStorage.getItem("idAcceuilTournoi"))
    $("input[data-preview]").change(() =>{
      // var data=[]
      console.log("fiel =====>",Array.from(document.getElementById("autreFile").files))
      var date=new Date()
      
      var my_files = Array.from(document.getElementById("autreFile").files)
        var fileNom=date.getFullYear()+""+date.getMonth()+""+date.getDay()+""+date.getSeconds()+""+date.getMilliseconds()+""+my_files[0].name
        this.setState({files_name:{file:my_files[0],fileName:fileNom},multimedia:{file:fileNom}})
        
        var fileReader = new FileReader();
        fileReader.readAsDataURL(my_files[0]);
        fileReader.onload = (fileEvent)=> {
          this.setState({files_preview:fileEvent.target.result})
    
        };

    
    });
  }
  
  handleAddEquipe=()=>{
    var media=null
    if(this.state.files_name.file!=""){
       media=this.state.multimedia
      storage.ref("images/"+this.state.files_name.fileName).put(this.state.files_name.file).then(()=>{})
     
    }
    var id=localStorage.getItem("idAcceuilTournoi")
    const prix=localStorage.getItem('prixTournoi');
    //array.push(this.state.tournois)
    let  token=  JSON.parse(localStorage.getItem('token_auth'))
    var data= {
      "nomEquipe": $("#nomequipe").val(),
      "nomEntreprise": $("#raisonsociale").val(),
      "typeEntreprise":this.state.type,
      "paye":false,
      "archive": false,
      "numeroTelephone":$("#numero").val(),
      "raisonSociale":$("#raisonsociale").val(),
       "tournois":null,
      "ville":this.state.ville,
      "niveau":"16ieme",
       "email":$("#email").val(),
      "nomEcole":"",
      "multimedia":media,
      "idTournoi":id
  
    }

    console.log("data a transmettre",data)

    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json','Accept': 'application/json',
      
    },
      body: JSON.stringify(data)
  };

  fetch(conf.url+"tournoi/add/equipe/"+id,requestOptions)
  .then((response) => response.json())
  .then((data) => {
     console.log("okkk====>",data)
    try{
      if(data.id){
        var date=token.user.nom+"-"+new Date().getUTCMilliseconds()

        this.handleAddTransaction(date,prix)

        localStorage.setItem("idEquipePaye",data.id)
        window.Payement(token.user.nom,token.user.prenom,token.user.email,prix) 
       //this.setState({show:true,success:true});
      }else{
       this.setState({show:true,success:false});
      }
   }catch(err){
     this.setState({show:true,success:false});
   }
   }
   
  );
    
  }  

  handleChangeVille = (e) => {
     
    console.log("adate =====>",e)
    try{
      this.setState({ville:e.label})
     
    }catch(ee){

    }
  
  }
  handleChangeType = (e) => {
     
    console.log("adate =====>",e)
    try{
      this.setState({type:e.label})
     
    }catch(ee){

    }
  
  }

  handleValidate=()=>{
    if(this.state.success){
      this.setState({ok: <Redirect to='/acceuil-visiteur'/>});
    }else{
      this.setState({show:false})
    }
    
  }

  handleAddTransaction=(number,montant)=>{
    const terrain = localStorage.getItem("idAcceuilTournoi")
    let  token =  JSON.parse(localStorage.getItem('token_auth'))
    
    var data = {
      "date":new Date(),
      "status":"PENDING",
      "order_number":number,
      "montant":montant,
      "typeTransaction":"PARTICIPER_TOURNOI"
    }


    console.log("data a transmettre",data)




    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json','Accept': 'application/json',
    
    },
      body: JSON.stringify(data)
  };
  
    fetch(conf.url+"user/add/transaction/user/"+token.user.id+"/tournoi/"+terrain,requestOptions)
    .then((response) => response.json())
    .then((data) => {
      localStorage.setItem("idTransactionVisiteur",data.id)
    })
  }
  

  render() {
    const animatedComponents = makeAnimated();

  return (
    <>

    <CContainer fluid>
    <Suspense fallback={loading}>
    {
     (!this.state.status)?
    (
    <CFade>
    {/* <TheHeaderVisiteur/> */}
    <ModalMessage
              show={this.state.show}
              success={this.state.success}
              boutton={this.state.boutton}
              handleValidate={this.handleValidate}
              message={this.state.message}
              titre={this.state.titre}
              onCloseClick={() => this.setState({ show: false })}
              />
      <div className="mt-5"></div>

      <section id="contact" class="contact">
        <div class="container">
        {this.state.ok}
          <div className="mt-5">
            <div class="section-title">
              <h2>Inscription tournoi inter-entreprise</h2>
            </div>
          </div>

          <div class="row">

            <div class="col-lg-12 mt-5 mt-lg-0 d-flex align-items-stretch">
              <div  class="php-email-form">

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="name">Raison sociale</label>
                    <input type="text" name="raisonsociale" class="form-control" id="raisonsociale"/>
                    <div class="validate"></div>
                  </div>

                  <div class="form-group col-md-6">
                    <label for="name">Nom de l'équipe</label>
                    <input type="text" class="form-control" name="nomequipe" id="nomequipe"/>
                    <div class="validate"></div>
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="name">Numéro de téléphone</label>
                    <input type="tel" name="numero" class="form-control" id="numero"/>
                    <div class="validate"></div>
                  </div>

                  <div class="form-group col-md-6">
                    <label for="name">Email</label>
                    <input type="email" class="form-control" name="email" id="email"/>
                    <div class="validate"></div>
                  </div>
                </div>

                
                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="name">Ville</label>
                    <ReactSelect
                      closeMenuOnSelect={true}
                      components={animatedComponents}
                      onChange={this.handleChangeVille}
                      options={ville}
                      placeholder="Ville"
                    />
                    <div class="validate"></div>
                  </div>

                  <div class="form-group col-md-6">
                    <label for="name">Type d'entreprise</label>
                    <ReactSelect
                      closeMenuOnSelect={true}
                      components={animatedComponents}
                      onChange={this.handleChangeType}
                      options={optionTypeEntreprise}
                      placeholder="Choisir le type d'entreprise"
                    />
                    <div class="validate"></div>
                  </div>
                </div>


                <div class="form-row">
                <div class="form-group col-md-6" >
                  <label for="name">Votre logo</label>
                  <Input
                    className="form-control"
                    type="file"
                     
                    data-preview=".preview"
                    placeholder="Votre logo"
                    id="autreFile"
                  />
                  </div>
                  <div class="form-group col-md-6" style={{float:"right"}}>
             
                    <img style={{width:"150px",heigth:"150px",marginTop:"20px"}} className="card-img-top" src={this.state.files_preview} />
                    <div class="validate"></div>
                  </div>
                </div>

                {/* <div class=" form-group form-check col-md-12">
                  <input class="form-check-input" type="checkbox" value="" id="flexCheckDefault"/>
                  <label class="form-check-label" for="flexCheckDefault">
                    J'accepte les Conditions d'utilisation de O-Terrain, ainsi que les règlements du tournoi.
                  </label>
                </div> */}

                {/* <Row className="mt-3 ml-4">
                  <div class="form-group col-md-12">
                    <input type="checkbox" value="" id="flexCheckDefault"/>
                    <label class="form-check-label" for="flexCheckDefault"> J'accepte les <a href="/#/cgu"><u>Conditions d'utilisation de O-Terrain </u></a>, ainsi que <a href="/#/reglements"><u>Les règlements du tournoi</u></a>.</label>
                  </div>
                </Row> */}

                {/* <Row className="mt-3 ml-4">
                  <div class="form-group col-md-12">
                    <input type="checkbox" value="" id="flexCheckDefault"/>
                    <label class="form-check-label" for="flexCheckDefault">Recevoir notre Newsletter.</label>
                  </div>
                </Row> */}

                <div class="form-check" className="mt-3 ml-4">
                  <input class="form-check-input" checked="true" required type="checkbox" value="" id="flexCheckDefault"/>
                  <label class="form-check-label" for="flexCheckDefault">
                    J'accepte les <a href="/#/reglements"><u>Les règlements du tournoi</u></a>.
                  </label>
                </div>
                
                <div class="form-check" className="mt-3 ml-4">
                  <input class="form-check-input" type="checkbox" value="" id="flexCheckDefault2"/>
                  <label class="form-check-label" for="flexCheckDefault2">
                    Recevoir notre Newsletter.
                  </label>
                </div>

                
                <div class="mb-3 mt-5">
                  <div class="loading">Chargement</div>
                  <div class="error-message"></div>
                  <div class="sent-message">Votre message a été envoyé. Merci!</div>
                </div>
                <div class="text-center"><button onClick={this.handleAddEquipe} id="submit">S'inscrire</button></div>
              </div>
            </div>

          </div>

        </div>
      </section>
      {/* <TheFooter/> */}
    </CFade>):(<>{this.state.page}</>)
     }
    </Suspense>
    </CContainer>

    </>
  )
}
}

export default InscriptionTournoiEntreprise
