import {
  Card,
  Col,
  // Container,
  Row,
  CardBody,
  CardImg,
  // CardHeader,
  // CardTitle,
  // FormGroup,
  // Label,
  // Button,
  // Form,
  // Input,
  // InputGroupAddon,
  // InputGroupText,
  // InputGroup,
  // Breadcrumb,
  // BreadcrumbItem,
} from "reactstrap"
// import CIcon from '@coreui/icons-react'

// import logo from '../../../src/assets/ot.png'
// import ProgrammeTournoiEcole from './ProgrammeTournoiEcole'
import ProgrammeTournoiEntreprise from './ProgrammeTournoiEntreprise'
import InscriptionTournoiEntreprise from './InscriptionTournoiEntreprise'
import ResultatTournoiEntreprise from './ProgrammeTournoiEntreprise'
import ReactDOM from 'react-dom';
import { CContainer, CFade } from '@coreui/react'

// import ReactSelect from 'react-select'
// import makeAnimated from 'react-select/animated';

// import conf from '../../conf.js';
// import { storage } from "../../firebase/firebase";
import $ from 'jquery'
// import  { Redirect } from 'react-router-dom'
// import ville from '../dict/ville.js'
// import HeaderBa from '../../containers/TheHeaderBa'
import { format } from 'date-fns';
// import TheHeaderVisiteur from '../../containers/TheHeaderVisiteur'
// import TheFooter from '../../containers/TheFooter'

// import Success from '../pages/messages/Success'


const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

class DetailTournoiEntreprise extends Component {
  constructor(props) {
      super(props)
      this.state = {
        ok:"",
        entreprise:JSON.parse(localStorage.getItem('detail_tournoi_visiteur')),
        logo:JSON.parse(localStorage.getItem('detail_tournoi_visiteur')).logo,
        participer:false,
        matchs:[]

      }
  }
  
  componentDidMount() {
    const data=JSON.parse(localStorage.getItem('detail_tournoi_visiteur'))
    this.setState({matchs:data.matchs})

    if( (new Date(this.state.entreprise.date_debut_inscription)<=new Date()) && (new Date(this.state.entreprise.date_fin_inscription)>=new Date()) )
      {
        this.setState({participer:true})
     }else{
      this.setState({participer:false})
     }
      

    try{
      $("#debut").val(format(new Date(data.date_debut),'dd/MM/yyyy'))
    }catch(error){
      $("#debut").val("Non définie")
    }

    try{
      $("#fin").val(format(new Date(data.date_fin),'dd/MM/yyyy'))
    }catch(error){
      $("#fin").val("Non définie")
    }

    try{
      $("#inscription").val(format(new Date(data.date_fin_inscription),'dd/MM/yyyy'))
    }catch(error){
      $("#inscription").val("Non définie")
    }

    try{
      $("#inscription-debut").val(format(new Date(data.date_debut_inscription),'dd/MM/yyyy'))
    }catch(error){
      $("#inscription").val("Non définie")
    }
  }
  handleProgramme=()=>{
    ReactDOM.render(<div>{<ProgrammeTournoiEntreprise matchs={this.state.matchs}/>}</div>, document.getElementById('page'));
  }
  handleParticiper=()=>{
    ReactDOM.render(<div>{<InscriptionTournoiEntreprise/>}</div>, document.getElementById('page'))
  }
  handleResultat=()=>{
    ReactDOM.render(<div>{<ResultatTournoiEntreprise matchs={this.state.matchs}/>}</div>, document.getElementById('page'))
  }

  render() {
    // const animatedComponents = makeAnimated();
    
    var today = new Date();
    var Annee = today.getFullYear()

  return (
    <>

    <CContainer fluid>
    {this.state.ok}
    <Suspense fallback={loading}>
    <CFade>
    {/* <TheHeaderVisiteur/> */}

      <div className="mt-5"></div>

      <section id="contact" class="contact">
        <div class="container">



          {/* Tournoi inter-écoles des écoles secondaires */}

          <div className="mt-5">
            <div class="section-title">
              <h2>Tournoi inter-entreprises - {Annee}</h2>
            </div>
          </div>

          <Row> 
            <Card>
              
              <CardBody>
                <CardImg style={{height:"200px"}} src={this.state.logo} alt="Image tournoi inter-entreprise" />
              
                <Row className="mt-4">
                  <Col> 
                    <form  

                      class="php-email-form" 
                    >
                      <div class="form-row">
                        <div class="form-group col-md-6">
                          <div className="mt-lg-5"> </div>
                          {/* <textarea type="text" class="form-control" name="nomequipe" id="nomequipe" readOnly/> */}
                          Participez à ce tournoi en vous inscrivant à seulement <strong> {this.state.entreprise.prix} FCFA </strong>. <br/>
                          Venez défier d'autres équipes
                          <div class="validate"></div>
                        </div>

                        <div class="form-group col-md-6" >
                          <h4 className="text-center"> Informations </h4>

                          <div class="form-group">
                            <label for="name">Date de début des inscriptions</label>
                            <input type="text" class="form-control" name="subject" id="inscription-debut" readOnly/>
                            <div class="validate"></div>
                          </div>

                          <div class="form-group">
                            <label for="name">Date de fin des inscriptions</label>
                            <input type="text" class="form-control" name="subject" id="inscription" readOnly/>
                            <div class="validate"></div>
                          </div>

                          <div class="form-group">
                            <label for="name">Début du tournoi</label>
                            <input type="text" class="form-control" name="subject" id="debut" readOnly/>
                            <div class="validate"></div>
                          </div>

                          <div class="form-group">
                            <label for="name">Fin du tournoi</label>
                            <input type="text" class="form-control" name="subject" id="fin" readOnly />
                            <div class="validate"></div>
                          </div>

                          {/* <div class="mb-3 mt-5">
                            <div class="loading">Chargement</div>
                            <div class="error-message"></div>
                            <div class="sent-message">Message</div>
                          </div> */}
                         {
                           (this.state.participer)?
                         (<>
                          <div class="text-center"><button id="submit" onClick={this.handleParticiper} >Participer</button> </div>
                        
                         </>):<div style={{display:"none"}} class="text-center" className="mt-3 text-center"> <button   id="submit" onClick={this.handleProgramme}> Programme des matchs</button>  <button id="submit" onClick={this.handleResultat} >Résultat</button> </div>
                         }
                        </div>
                      </div>
                    </form>
                  </Col>
                </Row>
              </CardBody>
            </Card>
          </Row>

        </div>
      </section>
      
      {/* <TheFooter/> */}

    </CFade>
    </Suspense>
    </CContainer>

    </>
  )
}
}

export default DetailTournoiEntreprise
