import {
  Card,
  Col,
  // Container,
  Row,
  Spinner,
  CardBody,
  // CardHeader,
  // CardTitle,
  // FormGroup,
  // Label,
  Button,
  // Form,
  // Input,
  // InputGroupAddon,
  // InputGroupText,
  // InputGroup,
  // Breadcrumb,
  // BreadcrumbItem,
} from "reactstrap"
// import CIcon from '@coreui/icons-react'

// import Carousel from 'react-bootstrap/Carousel'
import portfolio2 from '../dashboard/assets/img/portfolio/portfolio-2.jpg'
import { CContainer} from '@coreui/react'
// import  { Redirect } from 'react-router-dom'
import InscriptionTournoiEcole from './InscriptionTournoiEcole'
import DetailTournoiEcole from './DetailTournoiEcole'
import InscriptionTournoiEntreprise from './InscriptionTournoiEntreprise'
import DetailTournoiEntreprise from './DetailTournoiEntreprise'
import ReactDOM from 'react-dom';
// import conf from '../../conf.js';
import { storage } from "../../firebase/firebase";
// import $ from 'jquery'
// import TheHeaderVisiteur from '../../containers/TheHeaderVisiteur'
// import TheFooter from '../../containers/TheFooter'
import conf from '../../conf.js';


// const loading = (
//   <div className="pt-3 text-center">
//     <div className="sk-spinner sk-spinner-pulse"></div>
//   </div>
// )

class NosTournois extends Component {
  constructor(props) {
      super(props)
      this.state = {
        ok:"",
        tournoi:[],
        tournoi_init:[],
        page:<div style={{marginTop:"25%",marginBottom:"20%",textAlign:"center"}}>
        <Spinner type="grow" color="black" />
     </div>,
       status:true,
      }
      this.handleDetailTournois.bind(this)
      this.handleParticiperTournois.bind(this)
      this.handleRechercheSimple.bind(this)
  }
  
  componentDidMount() {

    const requestOptions = {
      method: 'GET',
     
    };

    fetch(conf.url+"tournoi/tournois",requestOptions)
    .then((response) => response.json())
    .then((data) => {
      console.log("data tournoi===>",data)
      this.setState({status:false})
      try{
        if(data.length){
          this.setState({tournoi:data,tournoi_init:data})
          data.map((el,idx)=>{
            try{
              if(el.multimedia.file!=undefined && el.multimedia.file!=''){
                storage.ref("images")
                .child(el.multimedia.file).getDownloadURL()
                .then((url) => {
                  
                    data[idx].multimedia.file=url
                    this.setState({tournoi:data,tournoi_init:data,status:false})
                
                })
                }
            }catch(error){

            }
          
           })
        }
        
       }catch(error){
        this.setState({tournoi:[]})
       }
     }
     
     );
  
  };


  handleParticiperTournois=(type,data)=>{
    localStorage.setItem('idAcceuilTournoi',data.id);
    localStorage.setItem('prixTournoi',data.prixPartSa);
   if(type=="ecole"){
    ReactDOM.render(<div>{<InscriptionTournoiEcole/>}</div>, document.getElementById('page'));
   }else{
    ReactDOM.render(<div>{<InscriptionTournoiEntreprise/>}</div>, document.getElementById('page'));
   }
 
  }
  handleDetailTournois=(type,data)=>{
    // var logo=""
    localStorage.setItem('detail_tournoi_visiteur', JSON.stringify({matchs:data.matchs,logo:data.multimedia.file,id:data.id,date_debut:data.dateDebut,
      date_fin:data.dateFin,date_fin_inscription:data.dateFinInscription,prix:data.prixPartSa,date_debut_inscription:data.dateDebutInscription}));


      if(type=="ecole"){


        ReactDOM.render(<div>{<DetailTournoiEcole/>}</div>, document.getElementById('page'));
     }else{
      
        ReactDOM.render(<div>{<DetailTournoiEntreprise/>}</div>, document.getElementById('page'));
     }
  
   
  }

    
  handleRechercheSimple=(e)=>{
    console.log("donee",e.target.value)
  
    if(e.target.value==""){
      this.setState({tournoi:this.state.tournoi_init})
    }else{
      var data=this.state.tournoi.filter((data) =>  JSON.stringify(data).toLowerCase().indexOf(e.target.value.toLowerCase()) !== -1)
      this.setState({tournoi:data})
    }
  
   


}

  render() {

    return (
      <>
      <CContainer fluid>
        {this.state.ok}
        {
        (!this.state.status)?

        (
          <>
        <Row className="mt-5"></Row>
        <Row className="mt-5"></Row>
        <Row className="mt-5"></Row>
        
        <div class="section-title mt-5">
          <h2>Nos tournois</h2>
        </div>
        <div class="container">
        <Row>
          <div className="form-outline" class="form">
            {/* <small>Recherche</small> */}
            <input
              onChange={this.handleRechercheSimple}
              type="search"
              className="form-control "
              placeholder="Saisissez votre recherche..."
              style={{borderRadius:"40px"}}
            />
          </div>
        </Row>

        <Row >
        {
        this.state.tournoi.length?
        this.state.tournoi.map((element,idx)=>(
        <Col lg="6" md="6" sm="12" className="mt-5">
          <Card className="block" id="block" style={{borderRadius:"25px",borderColor:"#4f8f3f",borderWidth:"5px",backgroundColor:"#FFFFF"}}>
            <CardBody>
              <figcaption class="text-center mt-2 mb-2 font-weight-bold">
                <span style={{backgroundColor:"#194D33",textAlign:'center',float:'center'}} class="badge rounded-pill text-center">{element.typeTournoi=="TOURNOI_INTER_ENTREPRISE"?"Tournoi inter entreprise":"Tournoi inter école"}</span>
              </figcaption>

              <figure class="terrain" className="text-center">
                {
                  (element.multimedia!=null && element.multimedia.file!='')?
                  (<img style={{width:"540px",height:"250px"}} src={element.multimedia.file} class="img-fluid" alt=""/>):
                  (<img style={{width:"540px",height:"250px"}} src={portfolio2} class="img-fluid" alt=""/>)
                }
                <figcaption class="text-center mt-2 font-weight-bold">{element.nom}</figcaption>
              </figure>
              {
                (element.typeTournoi==conf.type_tournoi.TOURNOI_INTER_ECOLE)?
                (
                  <div class="row">
                    <div class="col-lg-6 col-md-12 col-sm-12 mt-1 text-center">
                      <Button
                        color="success"
                        className="font-16 btn btn-success float-left"
                        block
                        onClick={this.handleDetailTournois.bind(this,"ecole",element)}
                      >
                        Détails
                      </Button>
                    </div>
                    <div class="col-lg-6 col-md-12 col-sm-12 mt-1 text-center">
                      <Button
                        color="success"
                        className="font-16 btn btn-success float-right"
                        block
                        onClick={this.handleParticiperTournois.bind(this,"ecole",element)}
                      >
                        Participer
                      </Button>
                    </div>
                  </div>
                ):(
                  <div class="row" >
                    <div class="col-lg-6 col-md-12 col-sm-12 mt-1 text-center">
                      <Button
                        color="success"
                        className="font-16 btn btn-success float-left"
                        block
                        onClick={this.handleDetailTournois.bind(this,"entreprise",element)}
                      >
                        Détails
                      </Button>
                    </div>
                    <div class="col-lg-6 col-md-12 col-sm-12 mt-1 text-center">
                      <Button
                        color="success"
                        className="font-16 btn btn-success float-right"
                        block
                        onClick={this.handleParticiperTournois.bind(this,"entreprise",element)}
                      >
                        Participer
                      </Button>
                    </div>
                  </div>
                )
              }
            </CardBody>
            </Card>
          </Col>
           )):<h1 style={{marginTop:30,textAlign:"center"}}>AUCUN TERRAIN TROUVE!!</h1>
          }
          </Row>
          </div>
          </>
          ):(<>{this.state.page}</>)
          }
          
</CContainer>
      </>
    )
  }
}

export default NosTournois
